body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #09203f;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

:root{
  --Rojo:#f43b47;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


html{
  font-size: 16px;
  scroll-behavior: smooth;
  overflow-x: hidden;
}


.header{
  width: 100%;
  height: 6rem;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  background-image: linear-gradient(to top, #09203f 0%, #14232e 100%);
  box-shadow: 0px 4px 6px 0px  rgba(0,0,0, 0.5);
  position: relative;
}

.header > .IconMenu{
  display: none;
  opacity: 0;
  visibility: hidden;
}

.header > h1{
  width: 100%;
  color: #fff;
  font-size: 2rem;
  text-align: center;
}

.header > nav{
  width: 70%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;

}

nav > a{
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  text-decoration: none;
  /* background-color: #eee;
  padding: .2rem .5rem; */
  color: #f43b47;
  /* border-radius: 10px; */
  transition: .3s;
}

nav > a > img{
  width: 1rem;
  margin-right: .3rem;
}


nav > a:hover{
  transform: scale(1.1);
  color: #eee;
}

nav > form{
  width: 10rem;
  height: auto;
  display: flex;
  flex-direction: row;
}

nav > form > input{
  font-size: .8rem;
  padding:.5rem 0 .5rem 2.5rem;
  border-radius: 20px 0 0 20px;
  border: 0px;
  outline: none;
}

nav > form >button{
  border: 0px;
  outline: none;
  background-color: #fff;
  border-radius: 0 20px 20px 0;
}

nav > form >button > img{
  width: 2.6rem;
  height: 2.6rem;
  background-color: #fff;
  border-radius: 0 20px 20px 0;
  padding: .7rem;
  cursor: pointer;
}



.TitleArticuls{
  width: 100%;
  margin: .5rem auto;
  text-align: center;
  color: #eee;
  font-size: 2.4rem;
}


.ContainerNews {
  width: 95%;
  height: auto;
  display: grid;
  grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr;
  column-gap: 2rem;
  flex-wrap: wrap;
  row-gap: 2rem;
  margin: 1rem auto;
  background-color: #09203f;
  
}

.ContainerNews > figure{
  width: 100%;
  height: 26rem;
  background-image: linear-gradient(to top, #09203f 0%, #14232e 100%);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 0px 5px 2px  rgba(0,0,0, 0.5);
  position: relative;
}

.ContainerNews > figure > img{
  width: 100%;
  height: 75%;
  border-radius: 10px 10px 0 0;
}

.ContainerNews > figure > figcaption{
  font-size: 1rem;
  width: 99%;
  margin: 0 auto;
  color: #eee;
  overflow-wrap: break-word;
}



.ContainerNews > figure >button{
  width:auto;
  margin: .5rem 0 0 0;
  background-color: #f43b47;
  padding: .2rem .4rem;
  transition: .3s;
  border-radius: 10px;
  border: thin solid transparent;
  color: #eee;
  cursor: pointer;
}


.ContainerNews > figure> div{
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  width: auto;
  padding: .5rem;
  background-color: rgba(15, 26, 34, 0.722);
  border-radius: 0 8px 0 10px;
}

.ContainerNews > figure> div> img{
  width: 15px;
  height: auto;
  margin-right: 5px;
}

.ContainerNews > figure> div>h6{
  font-size: 1rem;
  color: #eee;
}

.ContainerNews > figure> button:hover{
  transform: scale(1.1);
}

.ContainerNews > figure> button:focus{
  border: thin solid #eee;
}


/* Estilos Loader */
.ContainerLoader{
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ContainerLoader > h4{
  color: #eee;
  font-size: 1.5rem;
  margin-top: 1rem;
}

